@media (max-width: 531px) {
  footer .col-md-5 {
    text-align: center;
  }

  footer .nav {
    justify-content: center;
  }
}

#Sponsors h3 {
  text-align: center;
}

.sponsor-section {
  margin-top: 20px;
  padding: 20px;
}

.sponsor-logo {
  margin: 50px;
}

@media (max-width: 1000px) {
  #ubisoft {
    height: 100px;
  }

  #accenture {
    max-height: 50px;
  }

  #conseil {
    max-height: 60px;
  }

  .sponsor-logo {
    margin: 20px;
  }
}

footer {
  background-color: #e0cbef;
  width: 100%
}

.footer-box {
  float: center;
  box-sizing: border-box;
}

@media (min-width: 1000px) {
  .secondary-background {
    /* background-color: #a488e3; */
    background-color: #C5B3EE;
  }

  .secondary-text{
    color: #A78AE1;
  }

  .accent-background{
    background-color: #d1c5ed;
  }

  .primary-background {
    background-color: #8464CC;
  }

  .primary-text {
    color: #8464CC;
  }

  /* Place text in the middle of the image */

  .hero-text {
    text-align: center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    color: white;
  }
}

